import React, { Component } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import './NavBar.css'


class NavBar extends Component {	
	constructor() {
    super()
	    this.state = {
	      	className : 'navbar-burger burger',
	      	classNameItems : 'navbar-menu'
    	}
  	}	

	render() {
		const onClick = () => {
			if (this.state.className === 'navbar-burger burger is-active'){
				this.setState({className : 'navbar-burger burger'})
				if (this.state.classNameItems === 'navbar-menu is-active'){
					this.setState({classNameItems: 'navbar-menu'})
				}
			}else if (this.state.className === 'navbar-burger burger'){
				this.setState({className : 'navbar-burger burger is-active'})
				if (this.state.classNameItems === 'navbar-menu'){
					this.setState({classNameItems: 'navbar-menu is-active'})
				} 
		}}
		return(
			<nav className="navbar is-fixed-top is-info" role="navigation" aria-label="main navigation">
				<div className="navbar-brand">
				    <a className="navbar-item" href="/">
				      	<div className="fa-3x is-mobile">
						  	<FontAwesomeIcon className='is-mobile' icon='code' data-fa-transform="shrink-8" />
						</div> 
				    </a>

				    <a role="button" href='/' className={this.state.className} aria-label="menu" onClick={onClick} aria-expanded="false" data-target="navbarBasicExample">
					    <span aria-hidden="true"></span>
					    <span aria-hidden="true"></span>
					    <span aria-hidden="true"></span>
				    </a>
				</div>
				<div id="navbarBasicExample" className={this.state.classNameItems} onClick={onClick}>
				    <div className="navbar-end is-active">
				      	<a className="navbar-item is-active" style={{fontWeight : 'bold' , paddingRight:'15%'}} href="/">
				        	Home
				      	</a>

				      	<a className="navbar-item is-active" style={{fontWeight : 'bold' , paddingRight:'15%'}} href="/">
				        	Documentation
				      	</a>
				        <a className="navbar-item is-active" style={{fontWeight : 'bold' }} href="/">
				          	More
				        </a>
				</div>
			</div>
			</nav>
		)
	}
}		
	

export default NavBar ;