import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import './Intro.css';
import myFace from './myphoto.jpg';

const Intro = () => {
	return (
		<div className='hero is-spaced centered justified is-mobile' >{/*centered*/}
			<div className="tile is-ancestor  ">
			  	<div className="tile is-vertical is-8">
			    	<div className="tile">
			      		<div className="tile is-parent is-vertical ">
			        		<article className="tile is-child notification is-primary">
			          			<p className="title">Hi I 'm JS web-Dev</p>
			          			<p className="subtitle"> Currently looking for a new cooding adventure</p>
			        		</article>
					        <article className="tile is-child notification is-warning ">
					        	<p className="title">What am I capable of ?</p>
					          	<p className="subtitle">{`isn't that your question ?
					         		don't worry you will find the answer below `}
					          		<FontAwesomeIcon icon='angle-double-down' />
					          	</p>

					        </article>
			      		</div>
			      		<div className="tile is-parent">
					        <article className="tile is-child notification is-info">
					          	<p className="title">Kyrolos Magdy</p>
					          	<p className="subtitle">a full-Stack Web Developer</p>
			          			<figure className="image is-4by3">
			            			<img className='is-rounded' src={`${myFace}`} alt={`Your browser dosen't support this Img`}/>
			          			</figure>
			        		</article>
			     	 	</div>
			    	</div>
			    	<div className="tile is-parent">
				      	<article className="tile is-child notification is-danger">
					        <p className="title">our quote for today</p>
					        <p className="subtitle"> you only stop learning when you Die</p>
					        <div className="content">
					        Albert Einstein 
					        </div>
				      	</article>
			    	</div>
			  	</div>
			  	<div className="tile is-parent">
				    <article className="tile is-child notification is-success">
				      	<div className="content">
					        <p className="title">preferres remotely jobs</p>
					        <p className="subtitle">I 'm using Git so I can deals with teams remotly, you know in 2019
						        most programers dosen't go to work like office and all these traditional 
						        stuff they just working from there own office there own
						        home there own country that's one reason why I prefers working remotly</p>
					        <div className="content">
					           {/*Content*/} 
					        </div>
				      	</div>
			    	</article>
			  	</div>
			</div>
		</div>
	)
} 

export default Intro